import React from 'react';
import './App.css';
import {Route, withRouter} from "react-router-dom";
import Navbar from "./components/navbar/navbar";
// import DialogContainer from "./components/dialog/DialogContainer";
import UsersContainer from "./components/users/UsersContainer"
// import ProfileContainer from "./components/pro/profileConteiner";
import HeaderContainer from "./components/header/headerContainer";
import Login from "./components/login/login";
import {connect} from "react-redux";

import {compose} from "redux";
import {initializedThunkApp} from "./redux/app_reducer";
import Preloader from "./components/common/preloader";
import {SuspenseComponents, SuspenseHOC, withAuthredirectTest, withSuspense} from "./hoc/suspenseComponent";
import {withAuthredirect} from "./hoc/withAuthComponent";
import {TestTwo} from "./components/dialog/DialogContainer";
const DialogContainer = React.lazy(() => import('./components/dialog/DialogContainer'));
const ProfileContainer = React.lazy(() => import('./components/pro/profileConteiner'));
// const TestTwo = React.lazy(()=>import('./components/dialog/DialogContainer'));


class App extends React.Component{
    componentDidMount(){

        this.props.initiaizedAC();
    }
    render() {

        if (!this.props.initialized) return <Preloader/>
        return (

                <div className="wrapper">
                    <HeaderContainer/>
                    <Navbar/>
                    <div className="app_wrapper_content">
                        <Route path="/profile/:userId?" render={SuspenseComponents(ProfileContainer)
                            // return <React.Suspense fallback={<div>Loading...</div>}>
                            //     <ProfileContainer/>}
                            // </React.Suspense>
                       }/>

                        {/*<Route path="/dialog" component={SuspenseHOC(TestTwo)}/>*/}

                        <Route path="/dialog" render={()=>
                        {return <React.Suspense fallback={<div>Loading...</div>}>
                                 <DialogContainer/>
                                </React.Suspense>
                        }

                        }/>


                        <Route path="/users" render={() =>
                            <UsersContainer/>

                        }/>
                        <Route path="/login" render={() =>
                            <Login/>

                        }/>
                    </div>

                </div>

        )
    }
}
let mapStateToProps = (state)=>{
    return{
    initialized: state.AppPage.initialized
    }
}

export default compose(withRouter ,
    connect(mapStateToProps,{initiaizedAC:initializedThunkApp}))(App);
