import React from "react";
import s from "./pro.module.css";
import Myposts from "./mypost/myposts";
import Profile_Info from "./profileinfo/profileinfo";
import Post from "./mypost/posts/post";
import MypostsContainer from "./mypost/myposts_container";



const Profile = (props) =>{

    return(
        <div>

                <Profile_Info profile={props.profile}
                              status={props.status}
                              setUserUpdateStatus={props.setUserUpdateStatus}
                              getUserStatus={props.getUserStatus}
                                />
                <MypostsContainer />
        </div>



    );
}
export default Profile;