import s from "../mypost/mypost.module.css";
import React from "react";
import Post from "./posts/post";
import {Field, handleSubmit, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../util/validators/validators";
import {Textarea} from "../../common/fromsControll/formsControls";

const Myposts =(props) => {
    let element_post = props.postData.map( message => <Post message = {message.message}  like={message.like}/>

    );
    let newPostElement = React.createRef();


   let onSetMyPost = (value) => {

        let  text = value.myPost1;
       props.OnClickAddPost(text);
    }

    return(
        <div className={s.mypostone}>
            <h2>MY POST  </h2>

            <MyPostFormRedux onSubmit={onSetMyPost}/>

            <div className={s.posts}>
                {element_post}

              <Post/>
            </div>

        </div>

    );



}
const maxLehgth10 = maxLengthCreator(10);
export default Myposts;
const MyPostForm =(props)=>{
    return(
    <form onSubmit={props.handleSubmit} >
        <Field component={Textarea} name={"myPost1"}   validate={[required,maxLehgth10]} placeholder={"Please text"}/>
        <button >Add message1</button>
    </form>
    )
}
const MyPostFormRedux = reduxForm ({form:"myPost"})(MyPostForm);