import React from "react"
import {connect} from "react-redux";
import Redirect from "react-router-dom/es/Redirect";
let mapStateToSuspense = (state) => ({dialogData:state.dialogData,})

export let  SuspenseComponents = (Component)=>{

       return(props)=>{
           return <React.Suspense fallback={<div>Loading...</div>}>
                         <Component {...props}/>
                    </React.Suspense>
}

}
let mapStateToPropsRedirect = (state)=>({
    isAuth:state.auth.isAuth
});

export const withAuthredirectTest = (Component) => {

    class RedirectComponent extends React.Component {
        render(){
            if(this.props.isAuth === false) return <Redirect to={"/login"}/>
            return(
                <Component {...this.props} />
            );
        }
    }
    //обернули конектом свой хок что бы дать доступ к стату isAuth что бы не прокидывать ее каждый рарз в компоненту из которой вызывается данный хок

    let ConnectAuthRedirectComponent = connect(mapStateToPropsRedirect)(RedirectComponent);
    return ConnectAuthRedirectComponent;
}
export const SuspenseHOC =(Componentsss)=>{
    class  SuspenseWrapper extends React.Component{
        render(){
            return(
                <React.Suspense fallback={<div>Loading...</div>}>
                    <Componentsss {...this.props}/>
                </React.Suspense>
            )
        }
    }
    let ConnectSuspense = connect(mapStateToSuspense)(SuspenseWrapper);

    return ConnectSuspense

}