import {userAPI as instance, userAPI} from "../api_dal/api";
import {ChangeObjectArray} from "../util/objec_helper";

const FOLLOW ="FOLLOW";
const UNFOLLOW= "UNFOLLOW";
const SET_USERS = "SET_USERS";
const SET_CURRENT_PAGE =  "SET_CURRENT_PAGE";
const SET_TOTAL_COUNT = "SET_TOTAL_COUNT";
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';


let innialState = {
    // users:[
    //     {id:'1',photoUrl:'https://avatars.mds.yandex.net/get-pdb/1592033/bdf99512-fe57-48ce-b977-1119038ade70/s1200',followed:false, user:'Dmitry',status:'I am boss', location:{city:'Minsk',country:'Belarus'}},
    //     {id:'2',photoUrl:'https://avatars.mds.yandex.net/get-pdb/1592033/bdf99512-fe57-48ce-b977-1119038ade70/s1200',followed:true, user:'Vasa',status:'I am boss', location:{city:'Moscow',country:'Russia'}},
    //     {id:'3',photoUrl:'https://avatars.mds.yandex.net/get-pdb/1592033/bdf99512-fe57-48ce-b977-1119038ade70/s1200',followed:false, user:'Lena',status:'I am boss', location:{city:'Chelabinsk',country:'Russia'}},
    //     {id:'4',photoUrl:'https://avatars.mds.yandex.net/get-pdb/1592033/bdf99512-fe57-48ce-b977-1119038ade70/s1200',followed:true, user:'Sveta',status:'I am boss', location:{city:'London',country:'Fran'}},
    // ]
    users:[],
    pageSize:5,
    totalUserCount:0,
    curentPage:1,
    isFetching:false,
    testPage:null,
    followingInProgress:[]
}

const usersReducer = (state =  innialState, action)=>{
    switch (action.type) {
        case FOLLOW :
            return {
                ...state,
                users:ChangeObjectArray(state.users,action.userid,"id",{followed:true})
                // users:state.users.map(u =>
                // { if (u.id === action.userid){
                //     return {...u,followed:true}
                // } return u;
                //
                // })
            }
        case UNFOLLOW :
            return {
                ...state,
                users:ChangeObjectArray(state.users,action.userid,"id",{followed:false})
                // users:state.users.map(u =>
                // { if (u.id === action.userid){
                //     return {...u,followed:false}
                // } return u;
                //
                // })
            }
            //добавление юзер зверя склеивание массива

        case SET_USERS: {
            return  {
                ...state, users: [...action.users]
            }
        }


        case SET_CURRENT_PAGE :
            return { ...state,  curentPage: action.curentPage}

        case SET_TOTAL_COUNT :
            return { ...state,  totalUserCount: action.count}

        case TOGGLE_IS_FETCHING:
            return {...state, isFetching: action.isFetching}

        case TOGGLE_IS_FOLLOWING_PROGRESS:
            return {...state, followingInProgress: action.followingInProgress
            ? [...state.followingInProgress,action.userID]
            : state.followingInProgress.filter(id=>id!=action.userID)}

        default:
            return  state   ;
    }


}
export const  followSuccessAC  =(userid) =>({type:FOLLOW,userid})
export const  unfollowSuccessAC =(userid)=>({type:UNFOLLOW,userid})
export  const setUsersAC =(users)=>({type:SET_USERS, users})
export  const setCurrentPageAC =(curentPage)=>({type:SET_CURRENT_PAGE,curentPage})
export  const setTotalCountAC = (totalcount)=>({type:SET_TOTAL_COUNT,count:totalcount})
export  const setToggleIsFetchingAC = (isFetching)=>({type:TOGGLE_IS_FETCHING,isFetching})
export  const setToggleIsFollowingAC = (followingInProgress,userID)=>({type:TOGGLE_IS_FOLLOWING_PROGRESS,followingInProgress,userID})

export default usersReducer ;

export const getUsersApiThunkCreator = (curentPage,pageSize)=>{
   return (dispatch)=>{
       dispatch(setCurrentPageAC(curentPage));
        dispatch(setToggleIsFetchingAC(true));
        // dispatch(setTotalCountAC());
        instance.getUsers(curentPage,pageSize).then
        (data => {
            dispatch(setToggleIsFetchingAC(false));
            dispatch(setUsersAC(data.items));
            dispatch(setTotalCountAC(data.totalCount));
        });
    }
}
export const followUnfollowFlow = async (dispatch,apiMethod,ActionC,userId)=>{
    dispatch(setToggleIsFollowingAC(true, userId));
    let response = await apiMethod(userId);
    if (response.data.resultCode == 0) {
        dispatch(ActionC(userId));
    }
    dispatch(setToggleIsFollowingAC(false, userId));

}
