import React from "react";
import sw from "./styleUsers.module.css";
import {NavLink} from "react-router-dom";
import userPhoto from "../../img/default_foto_user.jpg";

export let User=(props)=>{
    let u = props.user
    return (
        <div key={u.id} className={sw.user_info}>
            <div className={sw.gogo}>
                {/*делаем ссылку навлинком*/}
                <NavLink to={'/profile/'+u.id}>
                    <img className={sw.foto}
                         src={u.photos.small != null ? u.photos.small : userPhoto}/>
                </NavLink>
                <div className={sw.follow}>

                    {u.followed? <button disabled={props.followingInProgress.some(id=> id === u.id)} onClick={() => {
                            props.unfollow(u.id)
                            // props.setToggleIsFollowing(true,u.id);
                            // userAPI.unfollow(u.id).then(response =>{
                            //     if(response.data.resultCode == 0){
                            //         props.unfollow(u.id);
                            //     }
                            //
                            //     props.setToggleIsFollowing(false,u.id);
                            //
                            // });
                            // props.unfollow(u.id)
                        }}>UnFollow</button>

                        : <button  disabled={props.followingInProgress.some(id=> id === u.id)} onClick={() => {
                            props.follow(u.id)
                            // props.setToggleIsFollowing(true,u.id);
                            // userAPI.follow(u.id).then(response => {
                            //    if(response.data.resultCode == 0){
                            //        props.follow(u.id);
                            //    }
                            //     props.setToggleIsFollowing(false,u.id);
                            // });

                            // props.follow(u.id)
                        }} >Follow</button>}


                </div>
            </div>


            <div className={sw.wrapper_info}>
                <div className="tyu">
                    <span> {u.name} </span>
                    <br/>
                    <span> {u.status} </span>
                </div>

                <div><span>
                         {/*{u.location.country} */}
                    </span>
                    <br/>
                    {/*<span> {u.location.city} </span>*/}
                </div>
            </div>


        </div>)

}