import React from 'react';
import s from "./dialog.module.css";
import {NavLink} from "react-router-dom";
import Redirect from "react-router-dom/es/Redirect";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../common/fromsControll/formsControls";
import {maxLengthCreator, required} from "../../util/validators/validators";
import {test} from "./DialogContainer";



const Dialog_Item = (props) => {
    return (
        <div className={s.dialog+' '+  s.active}>
            <div className={s.foto}> фото </div>
            <NavLink to={"/dialog/"+props.id} activeClassName={s.active}>  {props.name} </NavLink>
        </div>
    );
}


const Message_Item = (props) => {

    return (
        <div className={s.message}> {props.message}  </div>
    );
}

const Dialog =(props) => {

        let  elementData = props.dialogData.dialogData.map(dialog => <Dialog_Item name={dialog.name} key={dialog.id} id={dialog.id}/>);
        let elementMessage = props.dialogData.messageData.map(message => <Message_Item message={message.message} key={message.id}/>
        );

        let newMessageBody = props.dialogData.newMessageBody;


    const onSendMessageClick=()=>{
        props.onSendMessage();

    }

    const onSendMessageAlert=(value)=>{
        props.onSendMessage(value.newMessageBody);
    }

    // if  (props.isAuth == false) return <Redirect to={'/login'}/>
    return(

      <div className={s.dialogs}>
          {/*{test}*/}
          <div  className={s.dialogs_item}>
              {elementData}

          </div>
          <div className={s.messages}>
              {elementMessage}


          </div>
          <AddMessageFormRedux onSubmit={onSendMessageAlert}/>



      </div>
    );
}
export default  Dialog;
const maxLength = maxLengthCreator(50);
const AddMessageForm =(props)=>{

    return(
        <form onSubmit={props.handleSubmit}>
            <Field component={Textarea} name={"newMessageBody"} placeholder="Enter Your  message" validate={[required,maxLength]}/>

            <div> <button> Send </button> </div>
        </form>
    )
}
const AddMessageFormRedux = reduxForm({form:"dialogAddMessageForm"})(AddMessageForm)