import React from "react";
import  s from "./header.module.css";
import {NavLink, Redirect} from "react-router-dom";

const Header = (props) => {
let i=0;
    // if(i==0) return <Redirect to={'/edit'}/>
    return(
        <header className={s.header_top} >
            {props.isAuth ? <div>{ props.login}  <button onClick={()=>{ props.logout()
            }
            }> Log OUT</button> </div>  : <NavLink to={"/login"}> Login </NavLink>}
            <a href="#" className={s.logo}> <img src="https://systemservice96.ru/wp-content/uploads/2016/10/1280px-Hewlett-Packard_logo.svg.png"/></a>

            <button onClick={()=>{
                return <Redirect to={'/edit'}/>
            }}>OOPP</button>

        </header>
    );

}
export default Header;