import React from "react"
import {Field, reduxForm} from "redux-form";
import {createFields, Input, Textarea} from "../common/fromsControll/formsControls";
import {required} from "../../util/validators/validators";
import {connect} from "react-redux";
import {LoginThunkAC} from "../../redux/auth_reduce";
import {Redirect} from "react-router-dom";


let LoginForm = (props) => {
    return (

        <div>
            <form onSubmit={props.handleSubmit}>
                {createFields("email","login",Input,[required])}
                {createFields("password","password",Input,[required])}
                {createFields("rememberMe","",Input,[],"checkbox")}
                <button>send</button>
                {props.error&& <div> {props.error} </div>}
            </form>

        </div>
    )
}
//создаем хок .... компонетну вокруг LoginForm
let LoginReduxForm = reduxForm({form:'login'})(LoginForm);
let Login = (props) => {
    const onSubmit = (formData)=>{
        props.login(formData.email,formData.password,formData.rememberMe);

    }
    if (props.isAuth) return <Redirect to = {"/profile"} />
    return (
        <div>

            <h1>LOGIN</h1>
            <LoginReduxForm onSubmit={onSubmit}/>
        </div>
    );
}
let mapStateToProps = (state)=>({
    isAuth:state.auth.isAuth
})
export default connect(mapStateToProps,{login:LoginThunkAC})(Login);