import React from "react";
import sw from "./styleUsers.module.css"
import {Paginator} from "../common/paginator/paginator";
import {User} from "./user";

let Users =(props)=>{

    // let pagesCount = Math.ceil(props.totalUserCount / props.pageSize);
    // let  pages = [];
    // //  ВЫчисляем кол-во юзеров и наполняем массив
    // for (let i=1;  i <= pagesCount; i++){
    //     pages.push(i);
    // }
    return(
        <div className={sw.wrapper_contant} >
            <Paginator totalUserCount={props.totalUserCount}
                       pageSize={props.pageSize}
                       curentPage={props.curentPage}
                       onChangeCurrentPage={props.onChangeCurrentPage} />

            {props.users.map(u =>  <User user={u}
                                         followingInProgress={props.followingInProgress}
                                         unfollow={props.unfollow}
                                         follow={props.follow}/>
            )
            }
        </div>
    )
}
export default Users;
