import {getAuthUserDataThunkAC} from "./auth_reduce";

const SET_INITIALIZED = "SET_INITIALIZED";
let initiaState = {
    initialized: false
}

export let appReduxer = (state = initiaState, action) =>{
    switch (action.type) {
        case SET_INITIALIZED:
            return {
                ...state,initialized:true
            }
        default:return state

    }
}


let  initializedAC =()=>({type:"SET_INITIALIZED"});


export let initializedThunkApp =()=>(dispatch)=>{
     let promise = dispatch(getAuthUserDataThunkAC());

        Promise.all([promise]).then(()=>{dispatch(initializedAC())})

    // dispatch(initializedAC());
}