import React from "react";
import styles from "./formsControls.module.css"
import {Field} from "redux-form";
import {required} from "../../../util/validators/validators";

export const Textarea = ({input, meta, ...props}) => {
    return (
        <div>
            <textarea className={meta.touched && meta.error && styles.error} {...input}{...props}/>
            <div>
                {meta.touched && meta.error && <span className={styles.error_text}>{meta.error}</span>}
            </div>
        </div>
    )
};


export const Input = ({input, meta, ...props}) => {
    return (
        <div>
            <input className={meta.touched && meta.error && styles.error} {...input}{...props}/>
            <div>
                {meta.touched && meta.error && <span className={styles.error_text}>{meta.error}</span>}
            </div>
        </div>
    )
};
export let createFields = (name,placeholder,component,validate,type) => {
    return (
        <div>
            <Field name={name} placeholder={placeholder} component={component} validate={validate} type={type}/>
        </div>
    )
}
