import React, {useState} from "react";
import sw from "./paginator.module.css";


export let Paginator = (props) => {
    let pagesCount = Math.ceil(props.totalUserCount / props.pageSize);
    let countPortion = pagesCount/props.pageSize
    let pages = [];

    let [portionCurrent,setPortionCurrent] = useState(2);

    //  ВЫчисляем кол-во юзеров и наполняем массив
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }
    let left = (portionCurrent - 1)*props.pageSize;
    let right = (portionCurrent*props.pageSize)
    return (
        <div className="ert"> {

            // pages.map(p => {
            //     return (
            //         {/*<span className={this.props.currentPage === p && sw.selectPage}>{p}</span>*/},
            //             <span className={
            //                 //Выделяем выбранный элемент путем подмены класса
            //                 props.curentPage === p && sw.selectPage} onClick={() => {
            //                 props.onChangeCurrentPage(p)
            //                 }
            //
            //             }>{p}</span>
            //
            //     )
            // })

              portionCurrent > 1 && <button onClick={() => {
                setPortionCurrent(portionCurrent - 1)
            }}>prev</button>}

        {pages.map((item,i,pages) =>{
            if (i >= left && i < right) {
            return (
            <span className={
                //                 //Выделяем выбранный элемент путем подмены класса
                props.curentPage === item && sw.selectPage} onClick={() => {
                let test=document.querySelector(".ert");
                test.setAttribute("name","blue")
                debugger
                props.onChangeCurrentPage(item);


            }}>{item}</span>
            )

        }
        })}

        { portionCurrent <= countPortion && <button onClick={() => {
                setPortionCurrent(portionCurrent + 1);
            }}>next</button>}


        </div>
    )
}