import {applyMiddleware, combineReducers, compose, createStore} from "redux";
import profileReducer from "./profile_reducer";
import dialogReducer  from "./dialogs_reducer";
import sidebarReducer from "./sidebar_reducer";
import usersReduser from "./users_pr_reducer";
import authReducer from "./auth_reduce";
import thunkMiddleware from"redux-thunk";
import { reducer as formReducer } from 'redux-form'
import {appReduxer} from "./app_reducer";


let reducers = combineReducers({
        profilePage : profileReducer,
        dialogData  : dialogReducer,
        sidebar     : sidebarReducer,
        usersPage   : usersReduser,
        auth        : authReducer,
        form        : formReducer,
        AppPage     :appReduxer

});
 const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
 const store = createStore(reducers, composeEnhancers(
    applyMiddleware(thunkMiddleware)
));

// ниже описано подключение стандарного стора , а выше описание подключения для работы  redux dev tool
// let store = createStore(reducers, applyMiddleware(thunkMiddleware));
// window.store =store;

window.__store__= store;

export default store;