import {AuthAPI, userAPI} from "../api_dal/api";
import {stopSubmit} from 'redux-form'

const SET_USER_DATA = "SET_USER_DATA";

let innialState = {
    email: null,
    userId: null,
    login: '',
    isAuth: false
}

const authReducer = (state = innialState, action) => {

    switch (action.type) {
        case SET_USER_DATA :

            return {
                ...state,
                email: action.data.email,
                userId: action.data.id,
                login: action.data.login,
                isAuth: action.data.isAuth
            }
        default:
            return state;
    }


}
export default authReducer;
export const setUserDataAC = (data) => ({type: SET_USER_DATA, data: data})

