export const getAuthUserDataThunkAC = (data) =>{ return async (dispatch) => {
    let response = await AuthAPI.getUserData()
    console.log("все работает ")
    if (response.data.resultCode == false) {
        response.data.data['isAuth'] = true;
        dispatch(setUserDataAC(response.data.data))
    }
}

}

export const LoginThunkAC = (email, password, rememberMe) => {
    return async (dispatch) => {
        let response = await AuthAPI.login(email, password, rememberMe)
        if (response.data.resultCode == false) {
            console.log('getAuthUserDataThunkAC');
            dispatch(getAuthUserDataThunkAC())
        } else {
            let message = response.data.messages.length > 0 ? response.data.messages[0] : "some error";
            debugger
            dispatch(stopSubmit('login', {_error: message}));
        }
    }

}
export const LogoutThunkAC = () => {
    return async (dispatch) => {
        let response = await AuthAPI.logOut()
        if (response.data.resultCode == false) {
            let localdata = {
                email: null,
                userId: null,
                login: '',
                isAuth: false
            }
            dispatch(setUserDataAC(localdata));
        }

    }
}

