import axios from "axios";

const  instance = axios.create({
    headers:{"API-KEY":"e4155b5f-1dc4-4ac1-95cb-d1b077708e28"},
    withCredentials:true,
    baseURL:'https://social-network.samuraijs.com/api/1.0/',
})
//создаем оъект userAPI у которого есть метолд getUsers
export const userAPI = {
    getUsers(curentPage =1, pageSize =10){
        return instance.get(`users?page=${curentPage} 
        &count=${pageSize}`).then(response => {
                return response.data;

            }
        );
},
    follow(userId) {
      return  instance.post(`follow/${userId}`,{},{
           })
    },
    unfollow(userId){
        return(
         instance.delete(`https://social-network.samuraijs.com/api/1.0/follow/${userId}`)

    );
    },
    // getUserData(){
    //     return instance.get(`https://social-network.samuraijs.com/api/1.0/auth/me`)
    // },
    getUserProfile (userID){
      return  instance.get(`https://social-network.samuraijs.com/api/1.0/profile/`+userID)
    }



}
export  const AuthAPI ={
    getUserData(){
        return instance.get(`auth/me`)
    },
    login(email,password,rememberMe =false){

        let captcha = true;
      return instance.post('auth/login',{email,password,rememberMe,captcha})
    },
    logOut(){
        return instance.delete('auth/login')
    }
}
export const profileAPI ={
    getProfile(userId){return instance.get('profile/'+userId);},
    getStatus(userId){return instance.get('profile/status/'+userId);},
    updateStatus(status){return instance.put('profile/status/',{status:status})}
}
//передаем не весь response  а только тот который нужен компоненте