import React from  "react";
import s from "../pro.module.css";
import Preloader from "../../common/preloader";
import ProfileStatus from "./profilestatus/profilestatus";
import ProfileStatusHook from "./profilestatus/profileHook";


const Profile_Info =(props)=> {
   if(props.profile == null){

       return <Preloader/>
   }

    return(
        <div>
            <div className="image_content">
                {/*<img src="./konon.jpg"/>*/}
            </div>

            <div className={s.about}>

                <div className={s.my_foto}>
                    <img src={props.profile.photos.large}/>
                </div>
                <div className={s.data}>
                    <h3>Family {props.profile.fullName} </h3>
                    <p>Дата рождения</p>
                    <p>город</p>
                    <p>web site</p>
                    <ProfileStatusHook status={props.status}
                                   getUserStatus={props.getUserStatus}
                                   setUserUpdateStatus={props.setUserUpdateStatus}/>
                </div>

            </div>

        </div>
            );
}
export default Profile_Info;