import {profileAPI, userAPI} from "../api_dal/api";

const  ADD_POST    = 'addPost';
       // UPLOAD_TEXT = 'uploadtext';
const  SET_USER_PROFILE = 'SET_USER_PROFILE';
const  SET_USER_STATUS ='SET_USER_STATUS';
const DELETEPOST='DELETEPOST';
let initialState = {
    postData: [
        {message: 'Hello people', like: '5'},
        {message: 'hello animal', like: '10'},

    ],
    newText:'77',
    profile:{
        "aboutMe": "СЏ РєСЂСѓС‚Рѕ С‡СѓРІР°Рє 1001%",
        "contacts": {
            "facebook": "facebook.com",
            "website": null,
            "vk": "vk.com/dimych",
            "twitter": "https://twitter.com/@sdf",
            "instagram": "instagra.com/sds",
            "youtube": null,
            "github": "github.com",
            "mainLink": null
        },
        "lookingForAJob": true,
        "lookingForAJobDescription": "РЅРµ РёС‰Сѓ, Р° РґСѓСЂР°С‡СѓСЃСЊ",
        "fullName": "samurai d new name",
        "userId": 1402,
        "photos": {
            "small": "https://social-network.samuraijs.com/activecontent/images/users/2/user-small.jpg?v=0",
            "large": "https://social-network.samuraijs.com/activecontent/images/users/2/user.jpg?v=0"
        }
    },
    status:""

}

const  profileReducer = (state = initialState , action)=>{

    switch (action.type){
        case ADD_POST :
            let newPost={
                message:action.text,
                like:'7'
            };


            return {...state,
                    postData:[...state.postData,newPost],
                    // postData:[...state.postData,newPost],   stateCopy.postData.push(newPost); равнозначна
                    newText:''
                    }
        case SET_USER_PROFILE:
            return {...state, profile:action.profile}

        case SET_USER_STATUS:
            return {...state, status:action.status}
        case DELETEPOST:
            return {
                ...state, postData:[...state.postData.filter(p => p.like  != action.data)]
            }
        default : return state;
    }

}
export default  profileReducer;


export const addPostActionCreator = (text)=>{
    return ({
            type:ADD_POST, text:text
        }
    )
}
