export   const  deletePost = (data)=>{
    return({
        type:DELETEPOST
    })
}
export const setUserProfileAC = (profile)=>{
    return({

        type: SET_USER_PROFILE, profile
    });
}
export  const setUserStatusAC =(status)=>{
    return ({
        type:SET_USER_STATUS, status:status
    })
}

export  const getUserProfileThunk = (userId)=> {
    return (dispatch) => {
        userAPI.getUserProfile(userId).then(response => {
            dispatch(setUserProfileAC(response.data));

        })

    }
}
export  const getUserStatusThunkAC =(userId)=>
    async (dispatch)=>{
         let response = await profileAPI.getStatus(userId);
           dispatch(setUserStatusAC(response.data));
}
export  const setUserUpdateStatusThunkAC =(status)=>
    async (dispatch)=>{
        let response = await profileAPI.updateStatus(status)
            if(!response.data.resultCode) {dispatch(setUserStatusAC(status));
            }
}