const   SEND_MESSAGE = 'SEND_MESSAGE';

let initialState ={
    dialogData: [
        {id: '1', name: 'Dima'},
        {id: '2', name: 'ANDREY'},
        {id: '3', name: 'SVETA'},
        {id: '4', name: 'VALERA'}],

    messageData: [
        {id: '1', message: 'hi'},
        {id: '2', message: 'How is you'},
        {id: '3', message: 'Yo'},
        {id: '4', message: 'YO'}],


}

const dialogReducer =(state = initialState,action)=>{

    switch (action.type) {

        case SEND_MESSAGE:{
            let body = action.sendMessage;
            // let stateCopy={...state};
            //     stateCopy.newMessageBody = {...state.newMessageBody};
            //     stateCopy.messageData = [...state.messageData];
            // stateCopy.newMessageBody = "";
            // stateCopy.messageData.push({id: '5', message: body});
            return {...state,
                    newMessageBody:"",
                    messageData:[...state.messageData,{id: '5', message: body}] }}
        default: return state;

    }

}
export default dialogReducer;


export const sendMessageCreator = (sendMessage)=>{
    return ({
            type:SEND_MESSAGE,sendMessage:sendMessage
        }
    )
}